export default function fullScreenButtonTool() {
  const fullScreenBth = document.getElementById('full-screan-canvas');
  // const canvasAnimationFrame = document.getElementById('canvas-play-result');
  // const contextCanvasAnimationFrame = canvasAnimationFrame.getContext('2d');


  const videoElement = document.getElementById('canvas-play-result');

  function toggleFullScreen() {
    if (!document.mozFullScreen && !document.webkitFullScreen) {
      if (videoElement.mozRequestFullScreen) {
        videoElement.mozRequestFullScreen();
      } else {
        videoElement.webkitRequestFullScreen(Element.ALLOW_KEYBOARD_INPUT);
      }
    } else if (document.mozCancelFullScreen) {
      document.mozCancelFullScreen();
    } else {
      document.webkitCancelFullScreen();
    }
  }

  document.addEventListener('keydown', (event) => {
    console.log(event.key);
    if (event.altKey && event.key === 'p') {
      toggleFullScreen();
    }
  }, false);

  fullScreenBth.addEventListener('click', () => {
    toggleFullScreen();
  });
}
