import objFramesWithImages from '../data/ObjFramesWithDataImg';
import stateOFcanvas from '../data/State-canvas';

export default function paintRollerrTool() {
  const canvas = document.getElementById('main-canvas');
  const context = canvas.getContext('2d');
  const selectColor = document.querySelector('.select-color');
  const toolsChanges = document.querySelector('.tools-changes');
  let imgData = null;
  // const imgDataBeforePaintRoller = stateOFcanvas.state;

  // if (imgDataBeforePaintRoller !== undefined) {
  //   context.putImageData(imgDataBeforePaintRoller, 0, 0);
  // }

  function clear() {
    context.fillStyle = selectColor.value;
    context.fillRect(0, 0, 640, 640);
    canvas.removeEventListener('click', clear);
    const img = document.getElementById('main-canvas');
    const activeFrame = document.querySelector('.frame-active');
    const canvasFrame = document.getElementById(activeFrame.children[1].id);
    const contextCanvasFrame = canvasFrame.getContext('2d');
    const canvasActiveFrameId = canvasFrame.id;
    contextCanvasFrame.drawImage(img, 0, 0, 150, 150);
    imgData = context.getImageData(0, 0, 640, 640);
    objFramesWithImages[canvasActiveFrameId] = imgData;
    stateOFcanvas.state = imgData;
    return objFramesWithImages;
  }
  canvas.addEventListener('click', clear);
  toolsChanges.addEventListener('click', () => {
    canvas.removeEventListener('click', clear);
  });
}
