export default function colorPen() {
  const replaseColor = document.getElementById('replaseColor');
  const currentColor = document.getElementById('currentColor');
  const prevColor = document.getElementById('prevColor');
  localStorage.setItem('currentColor', currentColor.value);
  localStorage.setItem('prevColor', prevColor.value);


  function changeColorPlase() {
    const first = currentColor.value;
    const second = prevColor.value;
    currentColor.value = second;
    localStorage.setItem('currentColor', second);
    prevColor.value = first;
    localStorage.setItem('prevColor', first);
  }

  document.addEventListener('keydown', (event) => {
    if (event.key === 'x') {
      changeColorPlase();
    }
  });

  replaseColor.addEventListener('click', changeColorPlase);
}
