import AmountFrames from './Amount-frames';
import objFramesWithDataImg from '../data/ObjFramesWithDataImg';
import objFramesWithImg from '../data/ObjFramesWithImg';


export default class Frames {
  constructor() {
    this.listFrames = document.querySelector('.list-frames');
    this.firstFrame = document.getElementById('101');
    this.btnAddFrames = document.querySelector('.add-frame');
    this.arrFrame = [];
    this.arrFrame.push(this.firstFrame);
    this.btnAddFrames.setAttribute('id', 'addFrame');
    this.amountFrames = this.listFrames.children.length;
    this.canvas = document.getElementById('main-canvas');
    this.context = this.canvas.getContext('2d');
  }

  selectFrame() {
    let selectFrameForDraw = null;
    this.listFrames.addEventListener('click', (event) => {
      console.log('RUN selectFrame()');
      console.log(objFramesWithDataImg);

      const listOfFrames = document.querySelector('.list-frames');
      selectFrameForDraw = [event.path[1]];
      for (let i = 0; i < listOfFrames.children.length; i += 1) {
        this.listFrames.children[i].children[0].setAttribute('class', 'frame');
      }
      selectFrameForDraw[0].setAttribute('class', 'frame-active');
      const frameActive = document.querySelector('.frame-active');
      const canvasFrameActiveId = frameActive.children[1].id;
      // this.context.fillStyle = '#000000';
      // this.context.fillRect(0, 0, 640, 640);
      const canvas = document.getElementById('main-canvas');
      const context = canvas.getContext('2d');

      function serchDataSelectFrame(obj, idCanvasFrame) {
        let result = null;
        Object.keys(obj).forEach((key) => {
          const value = obj[key];
          if (key === idCanvasFrame) {
            result = value;
          }
        });
        return result;
      }

      const imgData = serchDataSelectFrame(objFramesWithDataImg, canvasFrameActiveId);
      context.putImageData(imgData, 0, 0);
    });
  }

  watchForFrames() {
    console.log('RUN watchForFrames()');

    const newAmountFrames = this.listFrames.children.length;
    const activFrame = this.listFrames.children[newAmountFrames - 1].children[0];
    // const canvasFrameActive = activFrame.children[1].id;
    // const contextCanvasActiveFrame = document.getElementById(canvasFrameActive).getContext('2d');
    // contextCanvasActiveFrame.fillStyle = '#000000';
    // contextCanvasActiveFrame.fillRect(0, 0, 640, 640);

    activFrame.setAttribute('class', 'frame-active');
    for (let i = 0; i < newAmountFrames - 1; i += 1) {
      this.listFrames.children[i].children[0].setAttribute('class', 'frame');
    }
    for (let i = 0; i < newAmountFrames; i += 1) {
      this.listFrames.children[i].setAttribute('id', i);
      this.listFrames.children[i].children[0].children[0].innerHTML = [i + 1];
    }

    // const canvasFrame = document.getElementById(activFrame.children[1].id);
    // const canvasActiveFrameId = canvasFrame.id;
    // const img = document.getElementById('main-canvas');
    // objFramesWithImg[canvasActiveFrameId] = img;
    console.log(objFramesWithImg);
    // rfreshCanvasAnimationFrame();

    const amountFramesNow = new AmountFrames(newAmountFrames);
    amountFramesNow.positionCursorAndAmountFrames();
    return activFrame;
  }

  addFrames() {
    this.btnAddFrames.addEventListener('click', () => {
      console.log('ADD NEW FRAME>>>>>>');

      const newFrame = document.createElement('li');
      const frame = document.createElement('div');
      const numberOfFrame = document.createElement('span');
      const canvasFrame = document.createElement('canvas');
      const btnDellFrame = document.createElement('button');
      const iconDellFrame = document.createElement('i');
      const copyFrame = document.createElement('button');
      const iconCopyFrame = document.createElement('i');
      const moveFrame = document.createElement('button');
      const iconMoveFrame = document.createElement('i');

      this.arrFrame.push(newFrame);
      newFrame.setAttribute('id', this.arrFrame.length - 1);
      this.listFrames.appendChild(newFrame);

      frame.setAttribute('class', 'frame');
      frame.setAttribute('id', this.arrFrame.length + 100);
      frame.setAttribute('class', 'frame-active');
      newFrame.appendChild(frame);

      numberOfFrame.setAttribute('class', 'number-frame');
      numberOfFrame.innerHTML = this.arrFrame.length;
      frame.appendChild(numberOfFrame);

      frame.appendChild(canvasFrame);
      canvasFrame.setAttribute('class', 'canvasFrame');
      canvasFrame.setAttribute('id', this.arrFrame.length + 200);
      canvasFrame.setAttribute('width', 150);
      canvasFrame.setAttribute('height', 150);
      canvasFrame.getContext('2d').fillStyle = '#8c78c3';
      canvasFrame.getContext('2d').fillRect(0, 0, 260, 260);

      btnDellFrame.setAttribute('class', 'dell-frame button-frame');
      btnDellFrame.setAttribute('id', this.arrFrame.length);
      iconDellFrame.setAttribute('class', 'far fa-trash-alt');
      btnDellFrame.appendChild(iconDellFrame);
      frame.appendChild(btnDellFrame);

      copyFrame.setAttribute('class', 'copy-frame button-frame');
      copyFrame.setAttribute('id', this.arrFrame.length);
      iconCopyFrame.setAttribute('class', 'far fa-clone');
      copyFrame.appendChild(iconCopyFrame);
      frame.appendChild(copyFrame);

      moveFrame.setAttribute('class', 'move-frame button-frame');
      moveFrame.setAttribute('id', this.arrFrame.length);
      iconMoveFrame.setAttribute('class', 'fas fa-arrows-alt');
      moveFrame.appendChild(iconMoveFrame);
      frame.appendChild(moveFrame);

      this.watchForFrames();
      this.context.fillStyle = '#8c78c3';
      this.context.fillRect(0, 0, 640, 640);
      console.log(objFramesWithImg);
    });
  }

  dellFrame() {
    document.addEventListener('click', (event) => {
      console.log('RUN DELL FRAME');
      const numberInlistFrames = this.listFrames.children.length;
      if (event.target.className === 'dell-frame button-frame' && numberInlistFrames > 1) {
        const liFordell = event.path[2];
        console.log(event.path[1].children[1].id);
        const canvasFrameId = event.path[1].children[1].id;
        delete objFramesWithImg[canvasFrameId];
        console.log(objFramesWithImg);
        // console.log(liFordell.children[1]);
        this.listFrames.removeChild(liFordell);
        this.watchForFrames();
      } else if (event.target.className === 'far fa-trash-alt' && numberInlistFrames > 1) {
        const liFordell = event.path[3];
        this.listFrames.removeChild(liFordell);
        this.watchForFrames();
      }
    });
  }
}
