export default function resetBorderButton() {
  const penToolBthn = document.getElementById('pen-tool');
  const paintBucketBthn = document.getElementById('paint-bucket');
  const eraserToolBthn = document.getElementById('eraser-tool');
  const rectangleToolBthn = document.getElementById('rectangle-tool');
  const paintRollerBthn = document.getElementById('paint-roller');
  const coloPickerBthn = document.getElementById('color-picker');
  const moveToolBthn = document.getElementById('move-tool');
  const circleToolBthn = document.getElementById('circle-tool');
  const arrBthns = [];
  arrBthns.push(penToolBthn, paintBucketBthn, eraserToolBthn, rectangleToolBthn);
  arrBthns.push(paintRollerBthn, coloPickerBthn, moveToolBthn, circleToolBthn);

  arrBthns.forEach((element) => {
    // eslint-disable-next-line no-param-reassign
    element.style.borderColor = '#cbcbcb';
  });
}
