export default function setBgForAllCanvas() {
  const canvas = document.getElementById('main-canvas');
  const context = canvas.getContext('2d');
  context.fillStyle = '#8c78c3';
  context.fillRect(0, 0, 640, 640);
  const canvasFrames = document.querySelector('.canvasFrame');
  const framesContext = canvasFrames.getContext('2d');
  framesContext.fillStyle = '#8c78c3';
  framesContext.fillRect(0, 0, 150, 150);
  const canvasAnimationFrame = document.querySelector('.canvas-animation');
  const animationFrameContext = canvasAnimationFrame.getContext('2d');
  animationFrameContext.fillStyle = '#8c78c3';
  animationFrameContext.fillRect(0, 0, 260, 260);
}
