import SizeCanvas from './SizeCanvas';
import Canvas from '../../screens/canvas/Canvas';
import eraserTools from '../tool/Eraser-tools';
import resetBorderButton from './ArrButtonsTool';
import paintRollerrTool from '../tool/PaintRoller-tool';
import keyboard from '../tool/Tools-Key';
import colorPickerTool from '../tool/Color-picker-tool';
import fullScreenButtonTool from '../tool/Full-screen-button';

export default function startDraw() {
  const resizeCanvas = document.querySelector('.resize-canvas');
  const toolsChanges = document.querySelector('.tools-changes');
  const eraserToolBthn = document.getElementById('eraser-tool');
  const paintRollerBthn = document.getElementById('paint-roller');
  const resize32 = document.getElementById('resize32');
  const penToolBthn = document.getElementById('pen-tool');
  const coloPickerBthn = document.getElementById('color-picker');
  fullScreenButtonTool();

  function selectActivTool(event) {
    // eslint-disable-next-line no-use-before-define
    resizeCanvas.removeEventListener('click', chooseSizeCanvas);
    keyboard(20, 20);
    resize32.style.borderColor = '#000000';

    if (event.target.className === 'fas fa-paint-roller' || event.target.id === 'paint-roller') {
      resetBorderButton();
      paintRollerBthn.style.borderColor = '#000000';
      paintRollerrTool();
    } else if (event.target.className === 'fas fa-pen' || event.target.id === 'pen-tool') {
      resetBorderButton();
      penToolBthn.style.borderColor = '#000000';
      console.log('32x32');
      const canvas = new Canvas(20, 20);
      canvas.pensilDraw();
    } else if (event.target.className === 'fas fa-eraser' || event.target.id === 'eraser-tool') {
      resetBorderButton();
      eraserToolBthn.style.borderColor = '#000000';
      eraserTools();
    } else if (event.target.className === 'fas fa-eye-dropper' || event.target.id === 'color-picker') {
      console.log('start Color-PICKER');
      resetBorderButton();
      coloPickerBthn.style.borderColor = '#000000';
      colorPickerTool();
    }
  }

  function chooseSizeCanvas(event) {
    // toolsChanges.removeEventListener('click', selectActivTool);
    resizeCanvas.removeEventListener('click', chooseSizeCanvas);
    if (event.target.id === 'resize32') {
      console.log('selekt 32x32 size');
      const activButton = event.target;
      penToolBthn.style.borderColor = '#000000';
      activButton.style.borderColor = '#000000';
      keyboard(20, 20);
      // controllerTools(20, 20);
      const sizeCanvas = new SizeCanvas('32x32');
      sizeCanvas.size();
    } else if (event.target.id === 'resize64') {
      console.log('selekt 64x64 size');
      const activButton = event.target;
      penToolBthn.style.borderColor = '#000000';
      activButton.style.borderColor = '#000000';
      keyboard(10, 10);
      // controllerTools(10, 10);
      const sizeCanvas = new SizeCanvas('64x64');
      sizeCanvas.size();
    } else if (event.target.id === 'resize128') {
      const activButton = event.target;
      penToolBthn.style.borderColor = '#000000';
      activButton.style.borderColor = '#000000';
      keyboard(5, 5);
      // controllerTools(5, 5);
      const sizeCanvas = new SizeCanvas('128x128');
      sizeCanvas.size();
    }
  }

  resizeCanvas.addEventListener('click', chooseSizeCanvas);
  toolsChanges.addEventListener('click', selectActivTool);
}
