import paintRollerrTool from './PaintRoller-tool';
import resetBorderButton from '../controller/ArrButtonsTool';
import Canvas from '../../screens/canvas/Canvas';
import colorPickerTool from './Color-picker-tool';

export default function keyboard(width, heith) {
  const paintRollerBthn = document.getElementById('paint-roller');
  const penToolBthn = document.getElementById('pen-tool');
  const coloPickerBthn = document.getElementById('color-picker');

  document.addEventListener('keydown', (event) => {
    if (event.key === 'a') {
      resetBorderButton();
      paintRollerBthn.style.borderColor = '#000000';
      paintRollerrTool();
    } else if (event.key === 'p') {
      resetBorderButton();
      penToolBthn.style.borderColor = '#000000';
      const canvas = new Canvas(width, heith);
      canvas.pensilDraw();
    } else if (event.key === 'o') {
      resetBorderButton();
      coloPickerBthn.style.borderColor = '#000000';
      colorPickerTool();
    }
  });
}
