/* eslint-disable prefer-template */
import objFramesWithImg from '../../components/data/ObjFramesWithImg';
import objFramesWithDataImg from '../../components/data/ObjFramesWithDataImg';
import stateOFcanvas from '../../components/data/State-canvas';
// import rfreshCanvasAnimationFrame from '../../components/controller/Animation-frame';

export default class Canvas {
  constructor(Width, Height) {
    this.Width = Width;
    this.Height = Height;
  }

  pensilDraw() {
    const canvas = document.getElementById('main-canvas');
    const context = canvas.getContext('2d');
    const currentColor = document.getElementById('currentColor');
    const toolsChanges = document.querySelector('.tools-changes');
    const canvasActiveFrame = document.querySelector('.frame-active');
    const canvasOfFrame = canvasActiveFrame.children[1];
    const contextCanvasFrameActive = canvasOfFrame.getContext('2d');
    canvas.width = 640;
    canvas.height = 640;
    const cellWidth = this.Width;
    const cellHeight = this.Height;
    const cellsInRow = Math.floor(canvas.width / this.Width);
    const cells = [];
    let filling = false;
    let imgData = null;

    function convertRgbInHex(r, g, b) {
      // eslint-disable-next-line no-bitwise
      return '#' + (0x1000000 + (r << 16) + (g << 8) + b).toString(16).substring(1);
    }
    function runColorPickerTool() {
      const color = contextCanvasFrameActive.getImageData(0, 0, 1, 1).data;
      console.log(color);
      const red = color[0];
      const green = color[1];
      const blue = color[2];
      return convertRgbInHex(red, green, blue);
    }
    const colorCanvas = runColorPickerTool();
    console.log(colorCanvas);

    for (let top = 0; top < canvas.width; top += cellWidth) {
      for (let left = 0; left < canvas.height; left += cellHeight) {
        const cell = {
          top,
          left,
          solid: false,
          fill(solid) {
            this.solid = solid;
            // console.log(solid);
            context.fillStyle = solid ? colorCanvas : currentColor.value;
            context.fillRect(this.top, this.left, cellWidth, cellHeight);
          },
        };
        cells.push(cell);
        cell.fill(true);
      }
    }

    function getCellByPosition(top, left) {
      const topIndex = Math.floor(top / cellHeight) * cellsInRow;
      const leftIndex = Math.floor(left / cellWidth);
      return cells[topIndex + leftIndex];
    }
    function fillCellAtPositionIfNeeded(x, y, fillingMode) {
      const cellUnderCursor = getCellByPosition(x, y);
      if (cellUnderCursor.solid !== fillingMode) {
        cellUnderCursor.fill(fillingMode);
      }
    }
    function handleMouseMove(event) {
      fillCellAtPositionIfNeeded(event.layerX, event.layerY, filling);
    }
    function handleMouseDown(event) {
      filling = !getCellByPosition(event.layerX, event.layerY).solid;
      fillCellAtPositionIfNeeded(event.layerX, event.layerY, filling);
      canvas.addEventListener('mousemove', handleMouseMove, false);
    }
    function handleMouseUp() {
      canvas.removeEventListener('mousemove', handleMouseMove);
      const selectFrame = document.querySelector('.frame-active');
      const img = document.getElementById('main-canvas');
      const canvasFrame = document.getElementById(selectFrame.children[1].id);
      const contextCanvasFrame = canvasFrame.getContext('2d');
      contextCanvasFrame.drawImage(img, 0, 0, 150, 150);
      imgData = context.getImageData(0, 0, 640, 640);
      const canvasActiveFrameId = canvasFrame.id;
      objFramesWithImg[canvasActiveFrameId] = img;
      objFramesWithDataImg[canvasActiveFrameId] = imgData;
      // data for user sesion
      stateOFcanvas.state = imgData;
      // rfreshCanvasAnimationFrame();
      console.log(objFramesWithImg);
    }

    toolsChanges.addEventListener('click', () => {
      canvas.removeEventListener('mousedown', handleMouseDown, false);
      canvas.removeEventListener('mouseup', handleMouseUp, false);
    });
    document.addEventListener('keydown', (event) => {
      console.log(event.key);
      if (event.key !== 'p') {
        canvas.removeEventListener('mousedown', handleMouseDown, false);
        canvas.removeEventListener('mouseup', handleMouseUp, false);
      }
    });
    canvas.addEventListener('mousedown', handleMouseDown, false);
    canvas.addEventListener('mouseup', handleMouseUp, false);
  }
}
