/* eslint-disable no-bitwise */
/* eslint-disable space-infix-ops */
export default function colorPickerTool() {
  const canvas = document.getElementById('main-canvas');
  const context = canvas.getContext('2d');
  const currentColor = document.getElementById('currentColor');
  const toolsChanges = document.querySelector('.tools-changes');

  function convertRgbInHex(r, g, b) {
    // eslint-disable-next-line no-bitwise
    // eslint-disable-next-line prefer-template
    return '#' + (0x1000000 + (r << 16) + (g << 8) + b).toString(16).substring(1);
  }
  function runColorPickerTool(event) {
    const color = context.getImageData(event.layerX, event.layerY, 1, 1).data;
    console.log(color);
    const red = color[0];
    const green = color[1];
    const blue = color[2];
    const hexColor = convertRgbInHex(red, green, blue);
    currentColor.value = hexColor;
  }
  canvas.addEventListener('click', runColorPickerTool);
  toolsChanges.addEventListener('click', () => {
    canvas.removeEventListener('click', runColorPickerTool);
  });
}
