export default class SizeCanvas {
  constructor(sizeCanvas, frames = 1) {
    this.sizeCanvas = sizeCanvas || '32x32';
    this.frames = frames;
  }

  size() {
    const sizeCanvas = document.querySelector('.size-canvas');
    const positionCursor = document.querySelector('.position-cursor');

    sizeCanvas.innerHTML = `[${this.sizeCanvas}]`;

    document.addEventListener('mousemove', (event) => {
      if (event.target.id === 'main-canvas') {
        positionCursor.innerHTML = `${event.layerX}:${event.layerY}`;
      } else if (event.target.id !== 'main-canvas') {
        positionCursor.innerHTML = '';
      }
    });
  }
}
